<template>
  <form-component
    v-if="form.show"
    title="DATOS TIPO VEHICULO"
    endPoint="tipo/create"
    @onCancel="onCloseForm"
    @onSubmitEnd="onCloseForm"
    :fields="form.fields"
  />

  <list-component
    loadPoint="tipo/list"
    @onCrearClick="onCrearClick"
    :fields="list.fields"
    v-else
  />
</template>

<script>
import FormComponent from "../components/FormComponent.vue";
import ListComponent from "../components/ListComponent.vue";
export default {
  name: "TipoVehiculo",

  components: {
    ListComponent,
    FormComponent,
  },

  data() {
    return {
      list: {
        fields: [
          {
            key: "IdTipo",
            label: "ID",
            sortable: true,
          },
          {
            key: "Clase",
            label: "CLASE",
            sortable: true,
          },
        ],
      },
      form: {
        show: false,
        fields: [
          {
            key: "clase",
            label: "CLASE",
            type: "text",
            value: null,
          },
        ],
      },
    };
  },

  methods: {
    onCrearClick() {
      this.form.show = true;
    },

    onCloseForm() {
      this.form.fields.forEach((element) => {
        element.value = null;
      });
      this.form.show = false;
    },
  },
};
</script>