<template>
  <div class="list-comtainer">
    <div class="d-flex p-2">
      <slot />

      <button
        class="btn btn-success btn-sm"
        type="button"
        @click="$emit('onCrearClick')"
        v-if="showNew !== false"
      >
        CREAR
      </button>
    </div>
    <b-table hover :items="items" :fields="fields" />
  </div>
</template>

<script>
import Service from "../service/Service";
export default {
  name: "ListComponent",

  props: {
    loadPoint: String,
    fields: Array,
    parameters: Object,
    showNew: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      isLoading: true,
      items: [],
    };
  },

  mounted() {
    this.load();
  },

  methods: {
    async load() {
      this.isLoading = true;
      try {
        var result = await Service.sendPost(
          this.loadPoint,
          this.parameters,
          this.$store.getters.token
        );
        this.items = result.response;
      } catch (error) {
        //
        alert(error);
      }
      this.isLoading = false;
    },
  },
};
</script>