<template>
  <div class="form-container">
    <form class="form-form card" v-on:submit.prevent="submit">
      <div class="card-header">{{ title }}</div>

      <div class="card-body">
        <b-form-group
          v-for="(item, index) in fields"
          :key="`${item.key}-${index}`"
          :label="item.label"
          class="mb-3"
        >
          <b-form-select
            v-model="item.value"
            class="form-control"
            required
            :options="item.options"
            v-if="item.type == 'select'"
          />

          <b-input
            class="form-control"
            :type="item.type"
            v-model="item.value"
            required
            v-else
          />
        </b-form-group>
      </div>

      <div
        class="card-footer d-flex justify-content-center mt-2 pt-2 border-top"
      >
        <button
          type="button"
          class="btn btn-danger me-2"
          size="sm"
          @click="$emit('onCancel')"
        >
          CANCELAR
        </button>
        <button type="submit" class="btn btn-success" size="sm">GUARDAR</button>
      </div>
    </form>
  </div>
</template>

<script>
import Service from "../service/Service";

export default {
  name: "FormComponent",

  props: {
    title: String,
    fields: Array,
    endPoint: String,
  },

  methods: {
    async submit() {
      var toSend = {};
      this.fields.forEach((element) => {
        toSend[element.key] = element.value;
      });
      try {
        var result = await Service.sendPost(
          this.endPoint,
          toSend,
          this.$store.getters.token
        );
        console.log(result);

        this.$emit("onSubmitEnd");
      } catch (error) {
        //
        alert(error);
      }
    },
  },
};
</script>