<template>
  <form-component
    v-if="form.show"
    title="DATOS PERSONA"
    endPoint="persona/create"
    @onCancel="onCloseForm"
    @onSubmitEnd="onCloseForm"
    :fields="form.fields"
  />

  <list-component
    loadPoint="persona/list"
    @onCrearClick="onCrearClick"
    :fields="list.fields"
    v-else
  />
</template>

<script>
import FormComponent from "../components/FormComponent.vue";
import ListComponent from "../components/ListComponent.vue";
export default {
  name: "Persona",

  components: {
    ListComponent,
    FormComponent,
  },

  data() {
    return {
      list: {
        fields: [
          {
            key: "IdPersona",
            label: "ID",
            sortable: true,
          },
          {
            key: "Nombre",
            label: "NOMBRE",
            sortable: true,
          },
        ],
      },
      form: {
        show: false,
        fields: [
          {
            key: "nombre",
            label: "NOMBRE",
            type: "text",
            value: "",
          },
        ],
      },
    };
  },

  methods: {
    onCrearClick() {
      this.form.show = true;
    },

    onCloseForm() {
      this.form.fields.forEach((element) => {
        element.value = "";
      });
      this.form.show = false;
    },
  },
};
</script>