<template>
  <form-component
    v-if="form.show"
    title="DATOS VEHICULO"
    endPoint="vehiculo/create"
    @onCancel="onCloseForm"
    @onSubmitEnd="onCloseForm"
    :fields="form.fields"
  />

  <div v-else>
    <b-form-group label="PERSONA">
      <b-form-select v-model="persona.selected" class="form-control">
        <b-form-select-option :value="null">
          Seleccione Persona...
        </b-form-select-option>

        <b-form-select-option
          v-for="item in persona.list"
          :key="`persona-${item.IdPersona}`"
          :value="item.IdPersona"
        >
          {{ item.Nombre }}
        </b-form-select-option>
      </b-form-select>
    </b-form-group>

    <list-component
      @onCrearClick="onCrearClick"
      :fields="list.fields"
      v-if="persona.selected !== null"
      :key="persona.selected"
      loadPoint="vehiculo/list"
      :parameters="getParameters()"
    >
    </list-component>
  </div>
</template>

<script>
import FormComponent from "../components/FormComponent.vue";
import ListComponent from "../components/ListComponent.vue";
import Service from "../service/Service";

var tiposVehiculos = [];

export default {
  name: "Vehiculo",

  components: {
    ListComponent,
    FormComponent,
  },

  data() {
    return {
      list: {
        fields: [
          {
            key: "IdVehiculo",
            label: "ID",
            sortable: true,
          },
          {
            key: "IdTipo",
            label: "TIPO",
            sortable: true,
            formatter: function (value) {
              // console.log(tiposVehiculos, value);
              const tipoVehiculo = tiposVehiculos.filter(function (r) {
                return r.IdTipo == value;
              })[0];
              if (typeof tipoVehiculo !== "undefined") {
                return tipoVehiculo.Clase;
              }
              return value;
            },
          },
          {
            key: "Marca",
            label: "MARCA",
            sortable: true,
          },
        ],
      },
      form: {
        show: false,
        fields: [
          {
            key: "idPersona",
            label: "PERSONA",
            type: "select",
            value: null,
            options: [],
          },
          {
            key: "idTipo",
            label: "TIPO",
            type: "select",
            value: null,
            options: [],
          },
          {
            key: "marca",
            label: "MARCA",
            type: "text",
            value: null,
            min: 1,
          },
        ],
      },
      persona: {
        list: [],
        selected: null,
      },
      tipo: {
        list: [],
        selected: null,
      },
    };
  },

  async beforeMount() {
    this.load();
  },

  methods: {
    async load() {
      try {
        var personas = await Service.sendPost(
          "persona/list",
          {},
          this.$store.getters.token
        );
        this.persona.list = personas.response;

        var tipos = await Service.sendPost(
          "tipo/list",
          {},
          this.$store.getters.token
        );
        this.tipo.list = tipos.response;
        tiposVehiculos = this.tipo.list;
      } catch (error) {
        //
        alert(error);
      }
    },

    getParameters() {
      return {
        idPersona: this.persona.selected,
      };
    },

    onCrearClick() {
      var optionsPersona = [
        {
          text: "Seleccione",
          value: null,
        },
      ];
      this.persona.list.forEach((element) => {
        optionsPersona.push({
          text: element.Nombre,
          value: element.IdPersona,
        });
      });
      this.form.fields[0].options = optionsPersona;
      this.form.fields[0].value = this.persona.selected;

      var optionsTipo = [
        {
          text: "Seleccione",
          value: null,
        },
      ];
      this.tipo.list.forEach((element) => {
        optionsTipo.push({
          text: element.Clase,
          value: element.IdTipo,
        });
      });
      this.form.fields[1].options = optionsTipo;

      this.form.show = true;
    },

    onCloseForm() {
      this.form.fields.forEach((element) => {
        element.value = null;
      });
      this.form.show = false;
    },
  },
};
</script>