<template>
  <div>
    <b-form-group label="PARQUEADERO">
      <b-form-select
        v-model="parqueadero.selected"
        :options="parqueadero.list"
        class="form-control"
      />
    </b-form-group>

    <div v-if="parqueadero.selected !== null">
      <b-form-group label="BAHIA">
        <b-form-select
          v-model="bahia.selected"
          :options="bahia.list"
          class="form-control"
        />
      </b-form-group>

      <div v-if="bahia.selected !== null">
        <div>
          <b-form-group label="PERSONA">
            <b-form-select
              v-model="persona.selected"
              :options="persona.list"
              class="form-control"
            />
          </b-form-group>

          <div v-if="persona.selected !== null">
            <b-form-group label="VEHICULO">
              <b-form-select
                v-model="vehiculo.selected"
                :options="vehiculo.list"
                class="form-control"
              />
            </b-form-group>

            <div v-if="vehiculo.selected !== null">
              <button
                class="btn btn-success mt-3"
                type="button"
                v-if="isFree"
                @click="ingresarvehiculo"
              >
                ASIGNAR VEHICULO A BAHIA
              </button>

              <button
                class="btn btn-success mt-3"
                type="button"
                v-else
                @click="salidavehiculo"
              >
                GENERAR SALIDA DE VEHICULO
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import FormComponent from "../components/FormComponent.vue";
// import ListComponent from "../components/ListComponent.vue";
import Service from "../service/Service";

var tiposVehiculos = [];

export default {
  name: "GestionParqueadero",

  components: {
    // ListComponent,
    // FormComponent,
  },

  data() {
    return {
      list: {
        fields: [
          {
            key: "IdVehiculo",
            label: "ID",
            sortable: true,
          },
          {
            key: "IdTipo",
            label: "TIPO",
            sortable: true,
            formatter: function (value) {
              // console.log(tiposVehiculos, value);
              const tipoVehiculo = tiposVehiculos.filter(function (r) {
                return r.IdTipo == value;
              })[0];
              if (typeof tipoVehiculo !== "undefined") {
                return tipoVehiculo.Clase;
              }
              return value;
            },
          },
          {
            key: "Marca",
            label: "MARCA",
            sortable: true,
          },
        ],
      },

      formEntrada: {
        show: false,
        fields: [
          {
            key: "idPersona",
            label: "PERSONA",
            type: "select",
            value: null,
            options: [],
          },
          {
            key: "idTipo",
            label: "TIPO",
            type: "select",
            value: null,
            options: [],
          },
          {
            key: "marca",
            label: "MARCA",
            type: "text",
            value: null,
            min: 1,
          },
        ],
      },

      parqueadero: {
        list: [],
        selected: null,
      },

      bahia: {
        list: [],
        selected: null,
      },

      persona: {
        list: [],
        selected: null,
      },

      vehiculo: {
        list: [],
        selected: null,
      },

      isFree: false,
    };
  },

  async beforeMount() {
    this.load();
  },

  watch: {
    "parqueadero.selected": function (newValue) {
      this.bahia.selected = null;
      this.bahia.list = [];
      if (newValue !== null) {
        this.loadBahias();
      }
    },

    "bahia.selected": function (newValue) {
      this.persona.selected = null;
      this.persona.list = [];
      if (newValue !== null) {
        var bahiaSelected = this.bahia.list.filter(function (r) {
          return r.value == newValue;
        })[0];

        if (bahiaSelected.IdVehiculo == null) {
          this.isFree = true;
        } //
        else {
          this.isFree = false;
          //
        }
        console.log(newValue, bahiaSelected, this.isFree);

        this.loadPersonas();
      }
    },

    "persona.selected": function (newValue) {
      this.vehiculo.selected = null;
      this.vehiculo.list = [];
      if (newValue !== null) {
        this.loadVehiculos();
      }
    },

    "vehiculo.selected": function (newValue) {
      console.log(newValue, this.isFree);
    },
  },

  methods: {
    async load() {
      try {
        var parqueaderos = await Service.sendPost(
          "parqueadero/list",
          {},
          this.$store.getters.token
        );
        // console.log(parqueaderos);

        this.parqueadero.list = [
          {
            value: null,
            text: "Seleccione Parqueadero",
          },
        ];
        const this_ = this;
        parqueaderos.response.forEach((element) => {
          this_.parqueadero.list.push({
            value: element.IdParqueadero,
            text: element.Nombre,
          });
        });
      } catch (error) {
        //
        alert(error);
      }
    },

    async loadBahias() {
      var bahias = await Service.sendPost(
        "bahia/list",
        {
          idParqueadero: this.parqueadero.selected,
        },
        this.$store.getters.token
      );
      console.log(bahias);

      this.bahia.list = [
        {
          value: null,
          text: "Seleccione Bahia",
        },
      ];
      const this_ = this;
      bahias.response.forEach((element) => {
        this_.bahia.list.push({
          value: element.IdBahia,
          text: element.IdBahia,
          Disponible: element.Disponible,
          IdVehiculo: element.IdVehiculo,
        });
      });
    },

    async loadPersonas() {
      var personas = await Service.sendPost(
        "persona/list",
        {},
        this.$store.getters.token
      );
      console.log(personas);

      this.persona.list = [
        {
          value: null,
          text: "Seleccione Persona",
        },
      ];
      const this_ = this;
      personas.response.forEach((element) => {
        this_.persona.list.push({
          value: element.IdPersona,
          text: element.Nombre,
        });
      });
    },

    async loadVehiculos() {
      var vehiculos = await Service.sendPost(
        "vehiculo/list",
        {
          idPersona: this.persona.selected,
        },
        this.$store.getters.token
      );
      console.log(vehiculos);

      this.vehiculo.list = [
        {
          value: null,
          text: "Seleccione Vehiculo",
        },
      ];
      const this_ = this;
      vehiculos.response.forEach((element) => {
        this_.vehiculo.list.push({
          value: element.IdVehiculo,
          text: element.Marca,
        });
      });
    },

    async ingresarvehiculo() {
      try {
        var result = await Service.sendPost(
          "gestionParqueadero/ingresarvehiculo",
          {
            idBahia: this.bahia.selected,
            idVehiculo: this.vehiculo.selected,
          },
          this.$store.getters.token
        );
        console.log(result);
        alert("Vehiculo ingresado a la Bahia");
        this.vehiculo.selected = null;
        this.persona.selected = null;
        this.bahia.selected = null;
        this.parqueadero.selected = null;
        this.load();
      } catch (error) {
        alert(error);
      }
    },

    async salidavehiculo() {
      try {
        var result = await Service.sendPost(
          "gestionParqueadero/salidavehiculo",
          {
            idVehiculo: this.vehiculo.selected,
          },
          this.$store.getters.token
        );
        console.log(result);
        alert("Vehiculo sacado de la Bahia");
        this.vehiculo.selected = null;
        this.persona.selected = null;
        this.bahia.selected = null;
        this.parqueadero.selected = null;
        this.load();
      } catch (error) {
        alert(error);
      }
    },
  },
};
</script>