<template>
  <form-component
    v-if="form.show"
    title="DATOS TARIFA"
    endPoint="tarifa/create"
    @onCancel="onCloseForm"
    @onSubmitEnd="onCloseForm"
    :fields="form.fields"
  />

  <div v-else>
    <list-component
      @onCrearClick="onCrearClick"
      :fields="list.fields"
      loadPoint="tarifa/list"
    >
    </list-component>
  </div>
</template>

<script>
import FormComponent from "../components/FormComponent.vue";
import ListComponent from "../components/ListComponent.vue";
import Service from "../service/Service";

var tiposVehiculos = [];

export default {
  name: "Tarifa",

  components: {
    ListComponent,
    FormComponent,
  },

  data() {
    return {
      list: {
        fields: [
          {
            key: "IdTarifa",
            label: "ID",
            sortable: true,
          },
          {
            key: "IdTipo",
            label: "TIPO",
            sortable: true,
            formatter: function (value) {
              // console.log(tiposVehiculos, value);
              const tipoVehiculo = tiposVehiculos.filter(function (r) {
                return r.IdTipo == value;
              })[0];
              if (typeof tipoVehiculo !== "undefined") {
                return tipoVehiculo.Clase;
              }
              return value;
            },
          },
          {
            key: "Costo",
            label: "COSTO",
            sortable: true,
          },
        ],
      },
      form: {
        show: false,
        fields: [
          {
            key: "idTipo",
            label: "TIPO",
            type: "select",
            value: null,
            options: [],
          },
          {
            key: "costo",
            label: "COSTO",
            type: "number",
            value: null,
            min: 1,
          },
        ],
      },
      tipo: {
        list: [],
        selected: null,
      },
    };
  },

  async beforeMount() {
    this.load();
  },

  methods: {
    async load() {
      try {
        var result = await Service.sendPost(
          "tipo/list",
          {},
          this.$store.getters.token
        );
        this.tipo.list = result.response;
        tiposVehiculos = this.tipo.list;
      } catch (error) {
        //
        alert(error);
      }
    },

    onCrearClick() {
      var options = [
        {
          text: "Seleccione",
          value: null,
        },
      ];
      this.tipo.list.forEach((element) => {
        options.push({
          text: element.Clase,
          value: element.IdTipo,
        });
      });
      this.form.fields[0].options = options;
      this.form.fields[0].value = this.tipo.selected;

      this.form.show = true;
    },

    onCloseForm() {
      this.form.fields.forEach((element) => {
        element.value = null;
      });
      this.form.show = false;
    },
  },
};
</script>