<template>
  <form-component
    v-if="form.show"
    title="DATOS BAHIA"
    endPoint="bahia/create"
    @onCancel="onCloseForm"
    @onSubmitEnd="onCloseForm"
    :fields="form.fields"
  />

  <div v-else>
    <b-form-group label="PARQUEADERO">
      <b-form-select v-model="parqueadero.selected" class="form-control">
        <b-form-select-option :value="null">
          Seleccione Parqueadero...
        </b-form-select-option>

        <b-form-select-option
          v-for="item in parqueadero.list"
          :key="`parqueadero-${item.IdParqueadero}`"
          :value="item.IdParqueadero"
        >
          {{ item.Nombre }}
        </b-form-select-option>
      </b-form-select>
    </b-form-group>

    <list-component
      @onCrearClick="onCrearClick"
      :fields="list.fields"
      v-if="parqueadero.selected !== null"
      :key="parqueadero.selected"
      loadPoint="bahia/list"
      :parameters="getParameters()"
    >
    </list-component>
  </div>
</template>

<script>
import FormComponent from "../components/FormComponent.vue";
import ListComponent from "../components/ListComponent.vue";
import Service from "../service/Service";

export default {
  name: "Bahia",

  components: {
    ListComponent,
    FormComponent,
  },

  data() {
    return {
      list: {
        fields: [
          {
            key: "IdBahia",
            label: "ID",
            sortable: true,
          },
          {
            key: "Disponible",
            label: "DISPONIBLE",
            sortable: true,
            formatter: function (value) {
              if (value) return "SI";
              return "NO";
            },
          },
        ],
      },
      form: {
        show: false,
        fields: [
          {
            key: "idParqueadero",
            label: "PARQUEADERO",
            type: "select",
            value: null,
            options: [],
          },
          {
            key: "cantidad",
            label: "CANTIDAD",
            type: "number",
            value: null,
            min: 1,
          },
        ],
      },
      parqueadero: {
        list: [],
        selected: null,
      },
    };
  },

  async beforeMount() {
    this.load();
  },

  methods: {
    async load() {
      try {
        var result = await Service.sendPost(
          "parqueadero/list",
          {},
          this.$store.getters.token
        );
        this.parqueadero.list = result.response;
      } catch (error) {
        //
        alert(error);
      }
    },

    getParameters() {
      return {
        idParqueadero: this.parqueadero.selected,
      };
    },

    onCrearClick() {
      var options = [
        {
          text: "Seleccione",
          value: null,
        },
      ];
      this.parqueadero.list.forEach((element) => {
        options.push({
          text: element.Nombre,
          value: element.IdParqueadero,
        });
      });
      this.form.fields[0].options = options;
      this.form.fields[0].value = this.parqueadero.selected;

      this.form.show = true;
    },

    onCloseForm() {
      this.form.fields.forEach((element) => {
        element.value = null;
      });
      this.form.show = false;
    },
  },
};
</script>